import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';

import { MessageService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { StatusTransacao } from 'src/app/shared/consts/Status.const';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { FiltroTransacao } from 'src/app/shared/interfaces/transacoes/FiltroTransacao';
import { TransacaoDTO } from 'src/app/shared/interfaces/transacoes/TransacaoDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { TransacaoService } from 'src/app/shared/services/transacao/transacao.service';
import { externalLinkHandle } from 'src/app/shared/utils/ExternalLink';
import { TransacaoDetailComponent } from '../transacao-detail/transacao-detail.component';

@Component({
  selector: 'app-transacao-main',
  templateUrl: './transacao-main.component.html',
  styleUrls: ['./transacao-main.component.scss'],
})
export class TransacaoMainComponent implements OnInit, OnDestroy {

  transacoes: TransacaoDTO[] = [];

  sortStatus = StatusTransacao;
  selectedStatus: any;

  sortLoterica: any[] = [];
  selectedLoterica: any;

  sortTipoTransacao: any[] = [];
  selectedTipoTransacao: any;

  sortUserInicial: any[] = [];
  selectedUserInicial: any;

  sortUserFinal: any[] = [];
  selectedUserFinal: any;

  isLoaded = false;

  disabledDropDown = true;
  disabledBTN = true;

  private subscriptions = new Subscription();

  constructor(
    private transacaoService: TransacaoService,
    private funcionarioService: FuncionarioService,
    private lotericaService: LotericaService,
    private modal: ModalController,
    private toastService: MessageService,

  ) { }

  ngOnInit() {
    this.loadLotericas();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  changeFiltro(event) {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.sortLoterica = data;
      });

    this.subscriptions.add(sub);
  }

  loadTipoTransacao() {
    const sub = this.transacaoService.listTipoTransacao()
      .subscribe(data => {
        this.sortTipoTransacao = data;
      });

    this.subscriptions.add(sub);
  }

  loadFuncionarios() {
    const sub = this.funcionarioService.listAllUsers(this.selectedLoterica.id)
      .subscribe(data => {
        this.sortUserInicial = data;
        this.sortUserFinal = data;
      });

    this.subscriptions.add(sub);
  }

  loadTransacoes({ loterica_id, status_transacao_cod, tipo_transacao_id, user_final_id, user_inicial_id, }: FiltroTransacao) {
    
    const sub = this.transacaoService.list({ loterica_id, status_transacao_cod, tipo_transacao_id, user_final_id, user_inicial_id, })
      .subscribe(data => {
        this.transacoes = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  detalhar(transacao: TransacaoDTO) {
    this.showModal(TransacaoDetailComponent, { transacao });
  }

  downloadAnexo(transacao: TransacaoDTO) {
    this.toastService.clear();
    if (transacao.anexo) {
      externalLinkHandle(transacao.anexo);
    } else {
      this.presentToast("warn", "Sem anexo", "A transação não possui anexo", Toast.mediumDuration);
    }
  }

  filtrar() {
    if (this.disabledBTN) {
      return;
    }

    this.loadTransacoes({
      loterica_id: this.selectedLoterica.id,
      status_transacao_cod: this.selectedStatus, tipo_transacao_id: this.selectedTipoTransacao,
      user_final_id: this.selectedUserFinal, user_inicial_id: this.selectedUserInicial
    });
  }

  listenLotericaDropDown(event) {
    this.disabledDropDown = true;
    this.disabledBTN = true;

    this.selectedUserFinal = undefined;
    this.selectedUserInicial = undefined;
    this.selectedTipoTransacao = undefined;
    this.selectedStatus = undefined;

    this.transacoes = [];
    const { value } = event;
    if (value) {
      this.loadTipoTransacao();
      this.loadFuncionarios();

      this.loadTransacoes({ loterica_id: value.id });

      this.disabledDropDown = false;
      this.disabledBTN = false;
    } else {
      this.selectedLoterica = null;
    }
  }

  listenStatusClear(event){
    this.selectedStatus = undefined;
  }

  setStatusField(transacao: TransacaoDTO) {
    const { status_transacao, justificativa } = transacao;
    if(status_transacao !== 'REJEITADO'){
      return status_transacao;
    }

    const justificativaText = justificativa || "Sem justificativa";

    return `${status_transacao} (${justificativaText})`
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      if (hasUpdate) {
        this.isLoaded = false;
        this.loadTransacoes({ loterica_id: this.selectedLoterica.id, });
        this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
      }
    });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
