import { Component, OnDestroy, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { saveAs } from 'file-saver';

import { MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { PerfilEnum } from 'src/app/shared/consts/Perfil.const';

import { Relatorios } from 'src/app/shared/consts/Relatorio.const';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { ReportService } from 'src/app/shared/services/relatorio/report.service';
import { TesourariaService } from 'src/app/shared/services/tesouraria/tesouraria.service';

@Component({
  selector: 'app-relatorio-main',
  templateUrl: './relatorio-main.component.html',
  styleUrls: ['./relatorio-main.component.scss'],
})
export class RelatorioMainComponent implements OnInit, OnDestroy {

  reportList = Relatorios;
  selectedReport;

  sortLotericas: SelectItem[] = [];
  operadores: UserDTO[] = [];

  data: any = {};

  touchUI = false;

  reportName = 'Relatório.xlsx';

  private subscriptions = new Subscription();

  constructor(
    private toastService: MessageService,
    private reportService: ReportService,
    private platform: Platform,
    private lotericaService: LotericaService,
    private funcionarioService: FuncionarioService,
    private storage: StorageService,
    private tesourariaService: TesourariaService,
  ) { }

  ngOnInit() {
    if (this.platform.is('mobile')) {
      this.touchUI = true;
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }


  async reportDownload() {

    try {
      await this.handleBeforeDown();
      const sub = this.reportService.getById(this.selectedReport, this.data)
        .subscribe((response) => {
          console.log(response);
          saveAs(response, this.reportName);
          this.presentToast('success', 'Download em andamento', 'Baixando relatório', Toast.mediumDuration);
        });
      this.subscriptions.add(sub);

    } catch (err) {

    }


  }

  getDisabled() {
    if (this.selectedReport == 4) {
      return (this.data.date && this.data.user_id) ? false : true;
    }

    if (this.selectedReport == 5) {
      return (this.data.date) ? false : true;
    }
  }

  handleReportDropChange(eventValue) {
    this.reportName = 'Relatório.xlsx';
    this.sortLotericas = [];
    this.operadores = [];
    this.data = {};

    if (eventValue == 4) {
      this.loadLotericas();
    }
  }

  async handleBeforeDown() {
    if (this.selectedReport == 4) {
      const operador = this.operadores.find((o) => o.id == this.data.user_id);
      this.reportName = `Fechamento do dia ${this.data.date.toLocaleDateString()} - ${operador.nome}.xlsx`;
    }

    if (this.selectedReport == 5) {
      this.reportName = `Fechamento do dia ${this.data.date.toLocaleDateString()} - [LOJA].xlsx`;
      try {
        await this.tesourariaService.doCalculationByDate(this.data.date).toPromise();
      } catch (e) {
        throw e;
      }
    }
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        const arr = data.map((l) => {
          const { id, nome, codigo } = l;
          return {
            value: id,
            label: `${nome} - ${codigo}`
          }
        });

        this.sortLotericas = [{ value: undefined, label: 'Selecione' }, ...arr];
      });
    this.subscriptions.add(sub);
  }

  loadOperadores(loterica_id) {
    if (!loterica_id) {
      this.operadores = [];
      this.data.user_id = undefined;
      return;
    }
    const sub = this.funcionarioService.listAllUsers(loterica_id, { perfil_nomes: [PerfilEnum.OPERADOR] })
      .subscribe(loggedUsers => {
        this.operadores = loggedUsers;
      });

    this.subscriptions.add(sub);
  }


  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
