import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { StatusTransacao } from 'src/app/shared/consts/Status.const';
import { TransacaoDTO } from 'src/app/shared/interfaces/transacoes/TransacaoDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { TransacaoService } from 'src/app/shared/services/transacao/transacao.service';

@Component({
  selector: 'app-transacao-detail',
  templateUrl: './transacao-detail.component.html',
  styleUrls: ['./transacao-detail.component.scss'],
})
export class TransacaoDetailComponent implements OnInit, OnDestroy {

  @Input()
  transacao: TransacaoDTO;

  form: FormGroup;
  listStatus = StatusTransacao;
  isLoaded = false;

  createdUserId = '';
  userWasCreated = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private funcionarioService: FuncionarioService,
    private transacaoService: TransacaoService,
    private modal: ModalController,
  ) {
  }

  ionViewWillEnter() {

  }

  ngOnInit() {
    const { nome, maquina_requisitada, maquina_solicitou, local_entrada, local_saida,
      terminal_input, status_transacao, quantidade_transacao, tipo_transacao, user_final,
      user_inicial, criadoEm, valor_transacao } = this.transacao;


    this.form = this.formBuilder.group({
      nome: [{ value: nome, disabled: true }, [Validators.required]],

      /*maquina_requisitada_nome: [{ value: maquina_requisitada.nome }, [Validators.required,]],
      maquina_requisitada_descricao: [{ value: maquina_requisitada.descricao }, [Validators.required,]],
      maquina_solicitou_nome: [{ value: maquina_solicitou.nome }, [Validators.required,]],
      maquina_solicitou_descricao: [{ value: maquina_solicitou.descricao }, [Validators.required,]],
      user_final_nome: [{ value: user_final.nome }, [Validators.required,]],
      user_final_cpf: [{ value: user_final.cpf }, [Validators.required,]],
      user_inicial_nome: [{ value: user_inicial.nome }, [Validators.required,]],
      user_inicial_cpf: [{ value: user_inicial.cpf }, [Validators.required,]],*/

      local_entrada: [{ value: local_entrada, disabled: true }, [Validators.required,]],
      local_saida: [{ value: local_saida, disabled: true }, [Validators.required,]],

      terminal_input: [{ value: terminal_input, disabled: true }, [Validators.required,]],

      status_transacao: [{ value: status_transacao, disabled: true }, [Validators.required,]],

      quantidade_transacao: [{ value: quantidade_transacao, disabled: true }, [Validators.required,]],
      valor_transacao: [{ value: valor_transacao, disabled: true }, [Validators.required,]],
      tipo_transacao: [{ value: tipo_transacao.nome, disabled: true }, [Validators.required,]],

      criadoEm: [{ value: this.getFormatedDate(), disabled: true }, [Validators.required,]],

      descricao: [{ value: this.getText(), disabled: true }, [Validators.required,]],
    });

    this.form.get("status_transacao").setValue(this.listStatus.find(status => status.nome == status_transacao));
    this.isLoaded = true;
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  update() {
    /* const { id } = this.user;
     const nome = this.form.get('nome').value;
     const email = this.form.get('email').value;
     const senha = this.form.get('senha').value;
     const is_admin = this.form.get('is_admin').value;
     const status = this.form.get('status').value.value;
     const { id: perfil_id } = this.form.get('perfil_id').value;
     const { id: unidadeLoterica } = this.form.get('loterica_id').value;
 
     const sub = this.transacaoService.updateUser({ nome, email, senha, is_admin, status, perfil_id, unidadeLoterica }, id)
       .subscribe(response => {
         this.fechar(true);
       });
     this.subscriptions.add(sub);*/
  }

  fechar(userWasCreated = false) {
    this.modal.dismiss(userWasCreated);
  }

  getText(): string {
    const { maquina_requisitada, maquina_solicitou, user_final,
      user_inicial } = this.transacao;

    return `Esta transação foi solicitada por ${user_inicial.nome} de CPF ${user_inicial.cpf} autenticado no patrimônio '${maquina_solicitou.nome}', para o usuario ${user_final.nome} de CPF ${user_final.cpf} autenticado no patrimônio '${maquina_requisitada.nome}'`
  }

  getFormatedDate() {
    const { criadoEm } = this.transacao;
    const formatedDate = new Date(criadoEm);
    return `${formatedDate.toLocaleDateString()} às ${formatedDate.toLocaleTimeString()}`;
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
